import React from 'react';
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';
import { FaCalendar, FaArrowRight } from 'react-icons/fa';

const articles = [
  {
    id: 1,
    title: 'Building Scalable React Applications',
    description: 'Learn how to structure large-scale React applications with best practices and proven patterns.',
    date: 'March 15, 2024',
    readTime: '5 min read',
    image: 'https://via.placeholder.com/600x400'
  },
  {
    id: 2,
    title: 'Modern CSS Techniques',
    description: 'Exploring the latest CSS features and how they can improve your web development workflow.',
    date: 'March 10, 2024',
    readTime: '4 min read',
    image: 'https://via.placeholder.com/600x400'
  },
  // Add more articles as needed
];

const Articles = () => {
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: { staggerChildren: 0.1 }
    }
  };

  return (
    <motion.div
      variants={containerVariants}
      initial="hidden"
      animate="visible"
      className="w-full max-w-6xl mx-auto py-12"
    >
      <h1 className="text-4xl font-bold mb-12">Latest Articles</h1>
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
        {articles.map((article) => (
          <ArticleCard key={article.id} article={article} />
        ))}
      </div>
    </motion.div>
  );
};

const ArticleCard = ({ article }) => (
  <motion.div
    variants={{
      hidden: { y: 20, opacity: 0 },
      visible: { y: 0, opacity: 1 }
    }}
  >
    <Link to={`/articles/${article.id}`}>
      <motion.div
        whileHover={{ y: -5 }}
        className="bg-darker rounded-xl overflow-hidden group"
      >
        <div className="relative overflow-hidden">
          <img 
            src={article.image} 
            alt={article.title}
            className="w-full h-48 object-cover transform group-hover:scale-105 transition-transform duration-300"
          />
          <div className="absolute inset-0 bg-primary opacity-0 group-hover:opacity-20 transition-opacity" />
        </div>
        <div className="p-6">
          <div className="flex items-center text-sm text-gray-400 mb-3">
            <FaCalendar className="mr-2" />
            <span>{article.date} Â· {article.readTime}</span>
          </div>
          <h3 className="text-xl font-bold mb-3 group-hover:text-primary transition-colors">
            {article.title}
          </h3>
          <p className="text-gray-400 mb-4 line-clamp-2">
            {article.description}
          </p>
          <div className="flex items-center text-primary">
            <span className="mr-2">Read More</span>
            <FaArrowRight className="transform group-hover:translate-x-2 transition-transform" />
          </div>
        </div>
      </motion.div>
    </Link>
  </motion.div>
);

export default Articles;
